@import url(reset.css);


.title_bar {
    .left_item {
        a {
            opacity: 1;
            font-size: 20px;
        }
    }
} 

/* main */
main {height: calc(100% - 10px);}
._background {
    margin-top: -64px; 
    height: 65vh; 
    background: url(../images/bg_default.png) no-repeat 0 0;
    background-size: cover;
    width: 100%;
}

/* 프로필이미지 */
._profile {
    display: flex;
    justify-content: center;
    position: relative;
    width: 100%;
}
._profile_img {
    position: absolute; 
    left: 50%;
    top: -65px;
    width: 100px;
    height: 100px;
    margin-left: -50px;
    border-radius: 50%;
}

.profile_cont {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 45px;
}
.profile_name {font-size: 16px;}
.profile_cont input {
    width: 148px;
    height: 24px;
    margin: 15px 0 20px;
    padding: 0 5px;
    text-align: center;
}
.profile_menu {display: flex;}
.profile_menu li {
    margin: 0 6px;
    a {
        display: flex;
        flex-direction: column;
        align-items: center;
        font-weight: bold;
    }
}
/* 이미지 2 */
.profile_menu li span {
    display: block;
    width: 52px;
    height: 52px;
    border-radius: 50%;
    line-height: 52px;
    text-align: center;
    margin-bottom: 15px;
    background-color: #f2f2f2;
    color: #513736;
    font-size: 20px;
}

.header_profile{background-color: transparent;}